import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'HomePage',
      component: () =>
        import ('@/components/HomePage')
    },
    {
      path: '/reading',
      name: 'ReadingPage',
      component: () =>
        import ('@/components/ReadingPage')
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



